.board .board__container {
  margin: 0 auto;
  width: 70%;
  padding: 40px;
}

.board .board__row {
  display: -ms-flexbox;
  display: flex;
  border: 1px solid;
}

.board .board__col {
  -ms-flex: 1 1;
      flex: 1 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  height: 60px;
  -webkit-transition: all .5s;
  -o-transition: all .5s;
  transition: all .5s;
}

.board__row:not(:last-child) {
  border-bottom: none;
}

.board__col:not(:last-child) {
  border-right: 1px solid;
}

.board__col--blue {
  background: #5fd2f1;
}

.board__col--black {
  background: rgba(50, 50, 50, 0.8);
}

.board__col--selected {
  background: #f2f2f2;
  -webkit-box-shadow: inset 0 0 15px rgba(0, 0, 0, 0.8);
          box-shadow: inset 0 0 15px rgba(0, 0, 0, 0.8);
}

.horse {
  display: block;
  width: 100%;
  padding: 11px;
  font-size: 30px;
  font-weight: bold;
  cursor: pointer;
  -webkit-transition: all .4s ease-in-out;
  -o-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out;
}

.horse--blue, .count--blue {
  color: #0fa2d1;
}

.horse--black, .count--black {
  color: #323232;
}

.horse:hover {
  -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.8);
          box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.8);
}



